import { __ } from '@wordpress/i18n';
import { useBlockProps, RichText } from '@wordpress/block-editor';
import { PanelBody, TextControl } from '@wordpress/components';
import { InspectorControls } from '@wordpress/block-editor';

export default function Edit({ attributes, setAttributes }) {
	const { prefixText, linkText, linkUrl } = attributes;
	const blockProps = useBlockProps();

	return (
		<div {...blockProps}>
			<InspectorControls>
				<PanelBody title={__('Link Settings', 'somira')}>
					<TextControl
						label={__('Prefix Text (Optional)', 'somira')}
						value={prefixText}
						onChange={(value) => setAttributes({ prefixText: value })}
						placeholder="Optional text before link..."
						help={__('Text that appears before the link', 'somira')}
					/>
					<TextControl
						label={__('Link URL', 'somira')}
						value={linkUrl}
						onChange={(value) => setAttributes({ linkUrl: value })}
						placeholder="https://example.com"
						help={__('Enter the URL this link should point to', 'somira')}
					/>
				</PanelBody>
			</InspectorControls>

			<div className="custom-link-editor">
				<div className="custom-link-container">
					{prefixText && (
						<span className="custom-link-prefix">{prefixText} </span>
					)}
					
					<RichText
						tagName="span"
						className="custom-link-text"
						value={linkText}
						onChange={(value) => setAttributes({ linkText: value })}
						placeholder={__('Enter link text...', 'somira')}
						withoutInteractiveFormatting
						allowedFormats={[]}
					/>
					
					<span 
						className="custom-link-arrow"
						style={{
							display: 'inline-block',
							width: '16px',
							height: '8px',
							marginLeft: '4px',
							backgroundImage: 'url(\'data:image/svg+xml;utf8,<svg width="31" height="12" viewBox="0 0 31 12" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M30.5303 6.53033C30.8232 6.23744 30.8232 5.76256 30.5303 5.46967L25.7574 0.696699C25.4645 0.403806 24.9896 0.403806 24.6967 0.696699C24.4038 0.989593 24.4038 1.46447 24.6967 1.75736L28.9393 6L24.6967 10.2426C24.4038 10.5355 24.4038 11.0104 24.6967 11.3033C24.9896 11.5962 25.4645 11.5962 25.7574 11.3033L30.5303 6.53033ZM0 6V6.75H30V6V5.25H0V6Z" fill="%23282828"/></svg>\')',
							backgroundRepeat: 'no-repeat',
							backgroundSize: 'contain',
							backgroundPosition: 'center',
							verticalAlign: 'middle'
						}}
					></span>
				</div>
				
				{linkUrl && (
					<p className="link-preview-note">
						{__('Link points to:', 'somira')} <code>{linkUrl}</code>
					</p>
				)}
			</div>
		</div>
	);
}